import { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./Schedule.css";

export const Schedule = () => {
  const [startDate, setStartDate] = useState<Date | null>(new Date());
  const [endDate, setEndDate] = useState<Date | null>(null); //  
  const [frequency, setFrequency] = useState<number>(1);

  const [visibleDates, setVisibleDates] = useState<number>(6); //  6    

  const makeSchedule = (startDate: Date, endDate: Date, frequency: number) => {
    const schedule = [];
    const currentDate = new Date(startDate); //   Date   startDate.       startDate

    while (currentDate <= endDate) {
      schedule.push(new Date(currentDate)); //    (currentDate)   schedule.     Date,      currentDate.
      currentDate.setDate(currentDate.getDate() + frequency); ////      frequency
      // setDate       currentDate,      frequency.
    }

    //  endDate   
    if (
      !schedule.some((date) => date.toDateString() === endDate.toDateString())
    ) {
      schedule.push(new Date(endDate));
    }
    return schedule;
  };

  return (
    <div className="schedule">
      <h3></h3>
      {" "}
      <DatePicker
        selected={startDate}
        onChange={(date) => setStartDate(date || new Date())}
        startDate={startDate ? startDate : undefined}
        minDate={new Date()}
        endDate={endDate ? endDate : undefined} //   endDate   endDate  DatePicker,     ,    endDate  null,    ,  undefined  null.    ,   endDate  DatePicker    Date | undefined,   Date | null
        dateFormat="dd/MM/yyyy"
      />
      {" "}
      <DatePicker
        selected={endDate}
        onChange={(date) => setEndDate(date)}
        startDate={startDate ? startDate : undefined}
        endDate={endDate ? endDate : undefined}
        minDate={startDate || new Date()} //   
        maxDate={
          new Date(
            new Date(startDate || new Date()).setMonth(
              (startDate || new Date()).getMonth() + 6
            )
          )
        } //     6  
        dateFormat="dd/MM/yyyy"
      />
      <select
        className="selected-period"
        value={frequency}
        onChange={(e) => setFrequency(+e.target.value)}
      >
        <option value={1}> </option>
        <option value={2}> 2 </option>
        <option value={3}> 3 </option>
        <option value={4}> 4 </option>
        <option value={5}> 5 </option>
        <option value={6}> 6 </option>
        <option value={7}> </option>
      </select>
      <ul>
        {endDate
          ? makeSchedule(startDate || new Date(), endDate, frequency)
              .slice(0, visibleDates)
              .map((date) => (
                <li key={date.toDateString()}>{date.toLocaleDateString()}</li>
              ))
          : null}
        {endDate &&
        makeSchedule(startDate || new Date(), endDate, frequency).length >
          visibleDates ? (
          <button
            className="show-more"
            onClick={() => setVisibleDates((prev) => prev + 6)}
          >
             
          </button>
        ) : null}
      </ul>
    </div>
  );
};
