import React, { useState } from "react";
import "./Registration.css";
import {createUserWithEmailAndPassword} from "firebase/auth";
import { auth } from "../index"; //   auth   index.tsx

interface RegistrationProps {
  showModal: boolean;
  setshowModal: React.Dispatch<React.SetStateAction<boolean>>;
}

export const Registration: React.FC<RegistrationProps> = ({
  showModal,
  setshowModal,
}) => {
  const [warning, setWarning] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [showPassword, setShowPassword] = useState<boolean>(false);
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");

  const register = async (email: string, password: string) => {
    try {
      const userObject = await createUserWithEmailAndPassword(
        auth,
        email,
        password
      ); //  
      const user = userObject.user;
      console.log("  ", user);
    } catch (error) {
      console.error(" ", error);
    }
  };

  const openshowModal = () => {
    setshowModal(true);
  };

  const closeshowModal = () => {
    setshowModal(false);
    setWarning("");
    setMessage("");
  };

  const passwordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const makeRegistrationOnFirebase = async (
    event: React.FormEvent<HTMLFormElement>
  ) => {
    event.preventDefault(); //  , event.preventDefault()                  JavaScript.       
    const necessaryPassword = /^(?=.*\d)[a-zA-Z]{6}/;

    if (!password.match(necessaryPassword)) {
      setWarning("    6    !");
      return; //      (   if else  try catch )
    }

    try {
      await register(email, password);
      setMessage("  !");
      setWarning("");
      //   
      setEmail("");
      setPassword("");
    } catch (error) {
      console.error(" ", error);
    }
  };

  return (
    <>
    <div className="registration-buttons">
     <button className="registration-button" onClick={openshowModal}>
        
      </button>
      <button className="sign-in-button">
        
      </button> 
    </div>
      {showModal ? (
        <div className="modal active">
          <form
            className="registration-form"
            onSubmit={makeRegistrationOnFirebase}
          >
            <div className="cross" onClick={closeshowModal}>
              
            </div>
            {" "}
            <input
              value={email}
              type="email"
              name="email"
              onChange={(e) => setEmail(e.target.value)}
            />
            {" "}
            <input
              value={password}
              type={showPassword ? "text" : "password"}
              name="password"
              className="password"
              onChange={(e) => setPassword(e.target.value)}
            />
            <img
              className="eye"
              src={`${process.env.PUBLIC_URL}/${
                showPassword ? "opened_eye.png" : "closed_eye.png"
              }`}
              alt={showPassword ? " " : " "}
              onClick={passwordVisibility}
            />
            <button type="submit"></button>
            {warning ? <h6>{warning}</h6> : null}
            {message ? <div className="message">{message}</div> : null}
          </form>
        </div>
      ) : null}
    </>
  );
};
